import PropTypes from 'prop-types';
import { useState } from 'react';
import Tooltip from '@mui/material/Tooltip';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import Confirm from '../../components/confirmation';

export default function DeleteMemoryIcon({ handleDeleteObject, snackBar, confirmMessage }) {
  const [confirm, setConfirm] = useState(false);

  const handleDelete = () => {
    console.log('handling delete');
    handleDeleteObject();
  };

  const handleClose = () => setConfirm(false);

  const handleClick = () => {
    console.log('handling click');
    setConfirm(true);
  };

  return (
    <>
      <Tooltip title="Delete">
        <IconButton
          aria-label="delete"
          sx={{
            bgcolor: 'form.input',
          }}
          onClick={handleClick}
        >
          <DeleteIcon />
        </IconButton>
      </Tooltip>
      <Confirm
        confirm={confirm}
        handleCallback={handleDelete}
        handleCloseCallback={handleClose}
        title={confirmMessage}
      />
      {snackBar}
    </>
  );
}

DeleteMemoryIcon.propTypes = {
  handleDeleteObject: PropTypes.func.isRequired,
  confirmMessage: PropTypes.string.isRequired,
  snackBar: PropTypes.node.isRequired,
};
