import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { Branding } from './brand';
import images from './images_list';

function Background({ children }) {
  const [pics, setPics] = useState(images);
  const [count, setCount] = useState(0);
  const [picGrid, setPicGrid] = useState(null);
  const rotateArray = (arr) => {
    const j = Math.floor(Math.random() * arr.length);
    return [...arr.slice(j), ...arr.slice(0, j)];
  };

  const picLoaded = () => setCount((cnt) => cnt + 1);

  function GetGridItems() {
    const imageContext = require.context('../static/images/bg', true);
    const pictureGrid = (
      <Grid
        container
        justifyContent="space-between"
        sx={{ bgcolor: 'form.main', position: 'relative' }}
      >
        {pics.map((pic) => (
          <Grid
            item
            md={1}
            xs={2}
            component="img"
            src={imageContext(pic.pic).default}
            key={pic.uuid}
            alt={pic.uuid}
            sx={{ paddingX: '1px', marginY: '1px', borderRadius: 2 }}
            onLoad={() => picLoaded()}
          />
        ))}
        {children}
      </Grid>
    );

    setPicGrid(pictureGrid);
  }

  const swapPic = (arr) => {
    const newArr = [...arr];
    const j = Math.floor(Math.random() * arr.length);
    const i = Math.floor(Math.random() * arr.length);
    [newArr[j], newArr[i]] = [newArr[i], newArr[j]];
    return newArr;
  };

  useEffect(() => setPics((arr) => rotateArray(arr)), [images]);

  useEffect(() => GetGridItems(pics), [pics]);

  useEffect(() => {
    const timer = setTimeout(() => setPics((arr) => swapPic(arr)), 2000);
    return () => clearTimeout(timer);
  });

  function getRandomInt(max) {
    return Math.floor(Math.random() * max);
  }

  const imageContext = require.context('../static/images/bg', true);
  return count >= 96 ? (
    picGrid
  ) : (
    <div>

      <Box
        sx={{
          position: 'absolute', top: '30%', left: '50%', transform: 'translate(-50%, -50%)',
        }}
      >
        <Branding />
        <Typography variant="h6" color="white" sx={{ paddingLeft: '3rem' }}>
          Is Loading ...
          [
          {count}
          %
          ]
        </Typography>
      </Box>

      <Box
        component="img"
        sx={{
          position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)',
        }}
        alt="test"
        src={imageContext(pics[getRandomInt(96)].pic).default}
      />

      <Box component="div" sx={{ display: 'none' }}>
        {picGrid}
        {children}
      </Box>

    </div>
  );
}

Background.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Background;
